"use client"
import React, { useState, useEffect, useRef } from "react";

const SIP_DOMAIN = "pbx2.telxio.com.sg";

const Dialer: React.FC = () => {
  const [JsSIP, setJsSIP] = useState<any>(null);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [number, setNumber] = useState("");
  const [ua, setUa] = useState<any>(null);
  const [session, setSession] = useState<any>(null);
  const [status, setStatus] = useState("Waiting for login...");
  const [isRegistered, setIsRegistered] = useState(false);
  const [isCalling, setIsCalling] = useState(false);

  const localAudioRef = useRef<HTMLAudioElement>(null);
  const remoteAudioRef = useRef<HTMLAudioElement>(null);

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ Load JsSIP dynamically ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  useEffect(() => {
    const loadJsSIP = async () => {
      const module = await import("jssip");
      setJsSIP(module.default);
    };
    loadJsSIP();
  }, []);

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ LOGIN HANDLER ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  const handleLogin = async () => {
    if (!username || !password) return alert("Enter SIP username and password");
    if (!JsSIP) return alert("JsSIP not yet loaded. Please wait a moment.");

    try {
      const socket = new JsSIP.WebSocketInterface(`wss://${SIP_DOMAIN}:8089/ws`);
      const configuration = {
        sockets: [socket],
        uri: `sip:${username}@${SIP_DOMAIN}`,
        authorization_user: username,
        password,
        register: true,
        display_name: username,
      } as any;

      const userAgent = new JsSIP.UA(configuration);
      userAgent.start();

      userAgent.on("connected", () => setStatus("Connected"));
      userAgent.on("disconnected", () => {
        setStatus("Disconnected");
        setIsRegistered(false);
      });
      userAgent.on("registered", () => {
        setStatus("Registered ‚úÖ");
        setIsRegistered(true);
        setIsLoggedIn(true);
      });
      userAgent.on("registrationFailed", () => {
        setStatus("Registration failed ‚ùå");
        setIsRegistered(false);
      });

      setUa(userAgent);
    } catch (err) {
      console.error("SIP configuration error", err);
      setStatus("SIP configuration error");
    }
  };

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ MAKE CALL ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  const makeCall = async () => {
    if (!number) return alert("Enter number");
    if (!ua) return alert("UA not initialized");
    if (!isRegistered) return alert("SIP not registered yet");

    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      if (localAudioRef.current) {
        localAudioRef.current.srcObject = stream;
      }

      const callSession = ua.call(`sip:${number}@${SIP_DOMAIN}`, {
        mediaConstraints: { audio: true, video: false },
        mediaStream: stream,
      });

      callSession.connection.addEventListener("track", (event: any) => {
        if (remoteAudioRef.current) {
          remoteAudioRef.current.srcObject = event.streams[0];
        }
      });

      callSession.on("progress", () => setStatus("Ringing..."));
      callSession.on("confirmed", () => {
        setStatus("In Call...");
        setIsCalling(true);
      });
      callSession.on("ended", () => {
        setStatus("Call Ended");
        setIsCalling(false);
        setNumber("");
      });
      callSession.on("failed", () => {
        setStatus("Call Failed ‚ùå");
        setIsCalling(false);
      });

      setSession(callSession);
    } catch (err) {
      console.error("Audio device error:", err);
      alert("Microphone access denied or unavailable.");
    }
  };

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ END CALL ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  const endCall = () => {
    if (session) {
      session.terminate();
      setStatus("Call Ended");
      setIsCalling(false);
    }
  };

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ LOGOUT HANDLER ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  const handleLogout = () => {
    if (ua) ua.stop();
    setUa(null);
    setIsLoggedIn(false);
    setIsRegistered(false);
    setUsername("");
    setPassword("");
    setNumber("");
    setStatus("Logged out");
  };

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ UI RENDER ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  if (!isLoggedIn) {
    return (
      <div className="flex min-h-[60vh] items-center justify-center">
        <div className="w-full max-w-sm rounded-lg border bg-card p-6 shadow-sm">
          <h2 className="mb-4 text-center text-xl font-semibold">üîê SIP Login</h2>
          <div className="space-y-3">
            <input
              className="w-full rounded-md border bg-background px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-primary"
              type="text"
              placeholder="SIP Username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
            />
            <input
              className="w-full rounded-md border bg-background px-3 py-2 text-sm outline-none focus:ring-2 focus:ring-primary"
              type="password"
              placeholder="SIP Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <button className="w-full rounded-md bg-primary px-3 py-2 text-sm font-medium text-primary-foreground hover:opacity-90" onClick={handleLogin}>Login</button>
            <p className="text-muted-foreground text-xs">{status}</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="mx-auto w-full max-w-lg space-y-4">
      <div className="flex items-center justify-between rounded-lg border bg-card p-3 text-sm">
        <div className="font-medium">{status}</div>
        <button className="rounded-md border px-3 py-1 text-xs hover:bg-muted" onClick={handleLogout}>
          Logout
        </button>
      </div>

      <input
        className="w-full rounded-md border bg-background px-3 py-3 text-lg tracking-widest outline-none focus:ring-2 focus:ring-primary"
        type="text"
        value={number}
        onChange={(e) => setNumber(e.target.value)}
        placeholder="Enter number"
        disabled={isCalling}
      />

      <div className="grid grid-cols-3 gap-3">
        {["1", "2", "3", "4", "5", "6", "7", "8", "9", "*", "0", "#"].map(
          (num) => (
            <button
              key={num}
              onClick={() => setNumber((prev) => prev + num)}
              className="rounded-lg bg-muted py-4 text-lg font-semibold hover:opacity-90"
            >
              {num}
            </button>
          )
        )}
      </div>

      <div className="flex justify-center">
        {!isCalling ? (
          <button
            className="rounded-full bg-green-600 px-6 py-3 text-white hover:bg-green-700 disabled:opacity-50"
            onClick={makeCall}
            disabled={!isRegistered}
          >
            üìû Call
          </button>
        ) : (
          <button className="rounded-full bg-red-600 px-6 py-3 text-white hover:bg-red-700" onClick={endCall}>
            üî¥ End
          </button>
        )}
      </div>

      <audio ref={localAudioRef} autoPlay muted />
      <audio ref={remoteAudioRef} autoPlay />
    </div>
  );
};

export default Dialer;
